import React, { createContext, useState, useEffect, useRef } from 'react';
import Echo from 'laravel-echo';
import { io } from 'socket.io-client';

export const ChatContext = createContext();

export const ChatProvider = ({ children }) => {
  const [messages, setMessages] = useState([]);
  const [typing, setTyping] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const echoRef = useRef(null);

  const fetchMessages = async (sessionId) => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`/api/sessions/${sessionId}/messages`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      });
      const data = await res.json();
      setMessages(data.data || []);
    } catch (err) {
      setError('Failed to load messages.');
    } finally {
      setLoading(false);
    }
  };

  const subscribeToSessionChannel = (sessionId) => {
    if (!sessionId) return;

    window.io = io;

    echoRef.current = new Echo({
      broadcaster: 'socket.io',
      host: 'http://localhost:6001',
      auth: {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      },
    });

    echoRef.current.private(`chat.${sessionId}`)
      .listen('.ChatMessageSent', (e) => {
        setMessages((prev) => [...prev, e.message]);
      });
  };

  const sendMessage = async (sessionId, text, sender) => {
    try {
      const res = await fetch(`/api/sessions/${sessionId}/messages`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
        body: JSON.stringify({
          message: text,
          type: 'text',
        }),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.message || 'Failed to send');

      setMessages((prev) => [...prev, data.message]);
    } catch (err) {
      setError('Failed to send message.');
    }
  };

  const setTypingStatus = () => {
    // Optional: implement whisper if needed
  };

  return (
    <ChatContext.Provider
      value={{
        messages,
        fetchMessages,
        sendMessage,
        subscribeToSessionChannel,
        typing,
        setTypingStatus,
        loading,
        error,
        setError,
      }}
    >
      {children}
    </ChatContext.Provider>
  );
};

